<template>
<div>

	<div>
		<v-btn small depressed color="grey lighten-4" @click="showHistory=false; showOrder=true">Orders</v-btn>
		<v-btn small depressed color="grey lighten-4" class="ml-5" @click="showHistory=true; showOrder=false">Histories</v-btn>
	</div>

	<v-card>
		<div class="mt-2">${{summary.profitSum.toFixed(2)}},   ({{summary.lastTime}})</div>
		<div>
			BuyCount : {{summary.buyCountTotal}},
			SellCount : {{summary.sellCountTotal}}
		</div>
		<div>
			BuyPrice : {{toFixed(summary.buyPriceTotal, 0)}},
			SellPrice : {{toFixed(summary.sellPriceTotal, 0)}}
		</div>
		<!-- <v-row>
			<v-col>
				<v-text-field
					v-model="editedItem.sell_percent"
					label="sell percent"
					dense
				></v-text-field>
			</v-col>
			<v-col>
				<v-btn
					x-small
					depressed
					@click="clickUpdatePercent()"
				>
					update
				</v-btn>
			</v-col>
		</v-row> -->
	</v-card>

	<div v-show="showOrder">

		<div class="mt-2">Summary</div>
		<v-data-table
			mobile-breakpoint="200"
			hide-default-footer
			:items-per-page="-1"
			:headers="last3hHeaders"
			:items="last3h"
			@click:row="clickSummaryRow"
			sort-by="buyPendingCount"
			sort-desc
			dense
			must-sort
		>
		</v-data-table>


		<v-card class="mt-3 pa-3 d-flex justify-space-between">
			<div>Orders</div>
			<v-btn
				color="primary"
				dark
				small
				class="mb-2"
				@click="clickSnackbarBtn('New')"
			>
				New Item
			</v-btn>
		</v-card>

		<v-data-table
			mobile-breakpoint="200"
			hide-default-footer
			:items-per-page="-1"
			:headers="headers"
			:items="orders"
			:sort-by="['symbol', 'buy_price']"
			:sort-desc="[true, true]"
			:search="searchOrderText"
			@click:row="clickOrderRow"
			multi-sort
			dense
		>
			<template v-slot:item.shortSymbol="{ item }">
				<small>{{item.shortSymbol}}</small>
			</template>

			<template v-slot:item.side="{ item }">
				<v-chip
					class="pa-1"
					:color="sideColor(item.side)"
					x-small
				>{{item.side}}</v-chip>
			</template>

			<template v-slot:item.buy_price="{ item }">
				<small class="text--secondary">{{item.buy_percent}} </small>
					<v-chip
						class="pa-1"
						:color="priceColor(item.buy_order_status)"
						x-small
					></v-chip>
				{{item.buy_price}}
			</template>

			<template v-slot:item.sell_percent="{ item }">
				<small class="text--secondary">{{item.sell_percent}} </small>
				<v-chip
					class="pa-1"
					:color="priceColor(item.sell_order_status)"
					x-small
				></v-chip>
				{{item.sell_price}}
			</template>
		</v-data-table>

		<!-- POSITIONS  START  -->
		<div class="mt-2">Postions</div>
		<v-data-table
			mobile-breakpoint="200"
			hide-default-footer
			:items-per-page="-1"
			:headers="postionHeaders"
			:items="positions"
			dense
		>
			<template v-slot:item.buy_price="{ item }">
				<v-chip
					class="pa-1"
					:color="priceColor(item.buy_order_status)"
					x-small
				></v-chip>
				{{item.buy_price}}
			</template>
			<template v-slot:item.sell_price="{ item }">
				<v-chip
					class="pa-1"
					:color="priceColor(item.sell_order_status)"
					x-small
				></v-chip>
				{{item.sell_price}}
			</template>
		</v-data-table>
	</div>
	<!-- POSITIONS  END  -->


	<histories v-show="showHistory" ref="historyView"></histories>

		<v-dialog
			v-model="itemDialog.show"
			max-width="500px"
		>
			<v-card>
				<v-card-text>
					<v-container>

						<div class=" mb-5"><b>{{ itemDialog.title }}</b></div>

						<v-row>
							<v-col
								cols="4"
								class="mt-0 pt-0  mb-0 pb-0"
							>
								<v-text-field
									v-model="editedItem.symbol"
									label="Symbol"
								></v-text-field>
							</v-col>
							<v-col
								cols="4"
								class="mt-0 pt-0  mb-0 pb-0"
							>
								<v-text-field
									v-model="editedItem.side"
									label="Side"
								></v-text-field>
							</v-col>

							<v-col
								cols="4"
								class="mt-0 pt-0  mb-0 pb-0"
							>
								<v-text-field
									v-model="editedItem.amt"
									label="Amt"
								></v-text-field>
							</v-col>

							<v-col
								cols="4"
								class="mt-0 pt-0  mb-0 pb-0"
							>
								<v-text-field
									v-model="editedItem.buy_percent"
									label="Buy percent"
								></v-text-field>
							</v-col>
							<v-col
								cols="4"
								class="mt-0 pt-0 mb-0 pb-0"
							>
								<v-text-field
									v-model="editedItem.sell_percent"
									label="Sell percent"
								></v-text-field>
							</v-col>
						</v-row>
						<v-row no-gutters class="ml-0">
							price : {{editedItem.buy_price}}
						</v-row>
					</v-container>
				</v-card-text>

				<v-card-actions class="mt-n8">
					<v-spacer ></v-spacer>
					<v-btn
						small
						color="grey lighten-3"
						class="mr-3 mb-3"
						@click="closeDialog"
					>
						Cancel
					</v-btn>

					<v-btn
						small
						class="mr-3 mb-3"
						color="blue darken-3"
						@click="clickDialogBtn()"
						v-if="itemDialog.mode === 'New'"
						dark
					>
						New
					</v-btn>

					<v-btn
						small
						class="mr-3 mb-3"
						color="primary"
						@click="clickDialogBtn()"
						v-else-if="itemDialog.mode === 'Edit'"
					>
						Edit
					</v-btn>

					<v-btn
						small
						class="mr-3 mb-3"
						color="red darken-3"
						@click="clickDialogBtn()"
						v-else-if="itemDialog.mode === 'Cancel'"
						dark
					>
						Delete
					</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>

	<v-snackbar
		v-model="showSnackBar"
		class="mb-8"
		color="grey lighten-5"
	>
		<div class="text-center">
			<v-btn
				small
				color="blue darken-3"
				@click="clickSnackbarBtn('New')"
			>
				new
			</v-btn>
			<v-btn
				small
				class="ml-5"
				color="primary"
				@click="clickSnackbarBtn('Edit')"
			>
				Edit
			</v-btn>
			<v-btn
				small
				class="ml-5"
				color="red darken-3"
				@click="clickSnackbarBtn('Cancel')"
			>
				Delete
			</v-btn>
		</div>
	</v-snackbar>

</div>

</template>

<script>

import Util from '../../Util'
import Histories from './Histories'

import HttpService from '@/services/HttpService'

export default {
	name: 'Mulbot',

	components: {
		Histories,
	},

	data: () => ({
		dialog: false,
		dialogDelete: false,
		headers: [
			{ text: '', value: 'shortSymbol'},
			{ text: 'Side', value: 'side' },
			{ text: 'Buy', value: 'buy_price', width:115},
			{ text: 'Sell', value: 'sell_percent', width:115},
			{ text: 'Amt', value: 'amt' },
			{ text: 'P', value: 'price' },
			{ text: 'Id', value: 'id', },
			{ text: '%', value: 'profit' },
		],

		postionHeaders: [
			{ text: '', value: 'shortSymbol', },
			{ text: 'Id', value: 'tribot_order_id', },
			{ text: 'Buy', value: 'buy_price' },
			{ text: 'Sell', value: 'sell_price' },
			{ text: '%', value: 'profit' },
		],

		historyHeaders: [
			{ text: '', value: 'shortSymbol', },
			{ text: 'Side', value: 'side' },
			{ text: 'Buy', value: 'buy_price' },
			{ text: 'Sell', value: 'sell_price' },
			{ text: 'profit', value: 'profit' },
			{ text: 'date', value: 'newDate' },
		],

		last3hHeaders: [
			{ text: '', value: 'shortSymbol', },
			{ text: 'Total', value: 'total', },
			{ text: 'New', value: 'buyNewCount', },
			{ text: 'Pending', value: 'buyPendingCount', },
		],

		desserts: [],
		editedIndex: -1,
		editedItem: {
			symbol: '',
			buy_price: '',
			sell_price: '',
		},
		orders:[],
		positions:[],
		histories:[],
		selected_item:{},
		profitSum :0,
		searchOrderText:'',

		showSnackBar: false,
		showHistory: false,
		showOrder: true,
		mode: null,

		itemDialog:{
			show:false,
			mode:null,
			title:'',
		},

		summary:{buyPriceTotal:0, sellPriceTotal:0,
		buyCountTotal:0, sellCountTotal:0,
		profitSum:0},

		last3h:[],
		tempLast3h:{}

	}),


	computed: {
		formTitle () {
			return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
		},
	},

	watch:{
		// editedItem: {
		//     deep: true,
		//     handler(){
		//         this.editedItem.profit = ((this.editedItem.sell_price - this.editedItem.buy_price) / this.editedItem.buy_price * 100).toFixed(2) + '%'
		//     }
		// }
	} ,

	created () {
		// this.initialize()
		this.getOrderList()


		let contracts = 163476
		let price = 0.7429

		let targetPrice = 10

		let purePrice = contracts * price
		let divider = purePrice / targetPrice

		console.log(purePrice)
		console.log(divider)

		let actualAmt =  (contracts / divider).toFixed(0)

		console.log(actualAmt)
		console.log(actualAmt * price)
	},


	methods: {

		async getOrderList () {

			this.summary.buyPriceTotal = 0
			this.summary.sellPriceTotal =0
			this.summary.buyCountTotal = 0
			this.summary.sellCountTotal = 0

			let orders = await HttpService.get('/coin/bybit/tribot/orders')
			let positions = await HttpService.get('/coin/bybit/tribot/positions')

			const ticker = await HttpService.get('/coin/bybit/tribot/ticker')


			this.tempLast3h = {}
			this.last3h = []


			for(let order of orders){

				if(this.tempLast3h[order.symbol] === undefined){
					this.tempLast3h[order.symbol] = {buyCount:0, sellCount:0, buyNewCount:0, buyPendingCount:0 }
				}

				let isFindPosition = false

				for(let position of positions){
					if(order.id === position.tribot_order_id){
						order.buy_order_status = position.buy_order_status
						order.buy_price = position.buy_price
						order.sell_order_status = position.sell_order_status
						order.sell_price = position.sell_price
						order.profit = (order.sell_price - order.buy_price) / order.buy_price * 100
						order.profit = this.toFixed(order.profit ,2)

						order.price = order.buy_price * order.amt
						order.price = this.toFixed(order.price ,2)

						if(order.side === 'Buy'){
							this.summary.buyPriceTotal += order.price
						}else{
							this.summary.sellPriceTotal += order.price
						}

						if(order.buy_order_status === 'New'){
							this.tempLast3h[order.symbol].buyNewCount++
						}
						isFindPosition = true
						break
					}
				}

				if(!isFindPosition){
					this.tempLast3h[order.symbol].buyPendingCount++
				}

				order.shortSymbol = order.symbol.split('USDT')[0]
			}

			for (const key in ticker) {
				const shortSymbol = key.split('USDT')[0]
				orders.push({symbol:key,
				shortSymbol: shortSymbol,
				buy_order_status: 'Ticker',
				buy_price: ticker[key].price,
				side:'---'})
			}


			this.orders = orders

			let tempPositions = []
			for(let position of positions){
				let isFind = false

				for(let order of orders){
					if(order.id === position.tribot_order_id){
						isFind = true
						break
					}
				}

				if(!isFind){
					position.shortSymbol = position.symbol.split('USDT')[0]
					position.profit =  ((position.sell_price - position.buy_price) / position.buy_price * 100).toFixed(2)
					tempPositions.push(position)
				}
			}

			for(let key in this.tempLast3h){
				const shortSymbol = key.split('USDT')[0]
				this.last3h.push({
					symbol:key,
					shortSymbol:shortSymbol,
					total:0,
					buyNewCount:this.tempLast3h[key].buyNewCount,
					buyPendingCount:this.tempLast3h[key].buyPendingCount,
				})
			}


			this.positions = tempPositions
			this.getHistories()
		},

		async getHistories () {
			let histories = await HttpService.get('/coin/bybit/tribot/histories')

			let tempProfitSum = 0


			for(let item of histories){
				tempProfitSum+=item.profit
				item.shortSymbol = item.symbol.split('USDT')[0]
				// item.profit =  ((item.sell_price - item.buy_price) / item.buy_price * 100).toFixed(2)
				item.newDate = this.date2str(new Date(item.created_date), 'hh:mm')
				this.summary.lastTime = item.newDate

				let time = new Date(item.created_date).getTime()

				if(item.side === 'Buy'){
					this.tempLast3h[item.symbol].buyCount++
				}else{
					this.tempLast3h[item.symbol].sellCount++
				}

				if(item.side === 'Buy'){
					this.summary.buyCountTotal++
				}else{
					this.summary.sellCountTotal++
				}
			}
			this.summary.profitSum = tempProfitSum

			// this.histories = histories
			this.$refs.historyView.setHistories(histories)

			for(const item of this.last3h){
				item.h3BuyCount = this.tempLast3h[item.symbol].buyCount
				item.h3SellCount = this.tempLast3h[item.symbol].sellCount
				item.total = item.h3BuyCount + item.h3SellCount
			}
		},

		priceColor (item) {
			let color = ''
			if(item === 'Created'){
				color = 'amber lighten-4'
			}else if(item === 'New'){
				color = 'amber lighten-2'
			}else if(item === 'Filled'){
				color = 'blue lighten-3'
			}else if(item === 'PartiallyFilled'){
				color = 'blue lighten-4'
			}else if(item === 'Cancelled'){
				color = 'red lighten-2'
			}else if(item === 'Ticker'){
				color = 'transparent'
			}else{
				color = 'transparent'
			}
			return color
		},

		sideColor (side) {
			let color = ''
			if(side === 'Buy'){
				color = 'green lighten-4'
			}else if(side === 'Sell'){
				color = 'red lighten-4'
			}else{
				color = 'white'
			}
			return color
		},

		clickSummaryRow (row) {
			this.searchOrderText = row.shortSymbol
		},

		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds()
			};
			y = y.replace(/(M+|d+|h+|m+|s+)/g, function(v) {
				return ((v.length > 1 ? "0" : "") + z[v.slice(-1)]).slice(-2)
			});

			return y.replace(/(y+)/g, function(v) {
				return x.getFullYear().toString().slice(-v.length)
			});
		},


		clickOrderRow (row) {
			this.showSnackBar = true
			this.editedItem = row
		},

		clickSnackbarBtn (mode) {
			if(mode ==='New'){
				this.itemDialog.title = 'New Item'
			}else if(mode ==='Edit'){
				this.itemDialog.title = 'Edit Item'
			}else if(mode ==='Cancel'){
				this.itemDialog.title = 'Cancel Item'
			}

			this.itemDialog.show = true
			this.itemDialog.mode = mode
			this.showSnackBar = false
		},

		async clickDialogBtn () {

			if(this.itemDialog.mode === 'New'){
				await HttpService.post('/coin/bybit/tribot/orders', this.editedItem)
				this.getOrderList()
			}else if(this.itemDialog.mode === 'Edit'){
				await HttpService.put('/coin/bybit/tribot/orders', this.editedItem)
				this.getOrderList()
			}else if(this.itemDialog.mode === 'Cancel'){

				await HttpService.delete('/coin/bybit/tribot/cancel/orders/'+this.editedItem.id)
				if(this.editedItem.buy_order_status === 'New'){
					this.editedItem.buy_order_status = 'CancelRequested'
				}else{
					this.getOrderList()
				}
			}
			this.closeDialog()
		},

		closeDialog () {
			this.itemDialog.show = false
			this.dialogDelete = false
			this.$nextTick(() => {
				this.editedIndex = -1
			})
		},

		async clickUpdatePercent () {
			await HttpService.put('/coin/bybit/tribot/orders/percent', this.editedItem)
		},


		toFixed:function(val, num){
			return Number(Number(val).toFixed(num))
		},

	},

}



</script>

