<template>

<div>

	<div class="mt-2">Position</div>
	<v-data-table
		mobile-breakpoint="200"
		hide-default-footer
		:items-per-page="-1"
		:headers="positionHeaders"
		:items="positions"
		dense
	>
	</v-data-table>

	<div class="mt-12">Strategy</div>
	<v-data-table
		mobile-breakpoint="200"
		hide-default-footer
		:items-per-page="-1"
		:headers="strategyHeaders"
		:items="strategies"
		dense
	>
	</v-data-table>

</div>

</template>

<script>

import Util from '../../Util'

export default {
	name: 'Mulbot',

	components: {
	},

	data: () => ({
		strategyHeaders: [
			{ text: '', value: 'symbol' },
			{ text: 'Frame', value: 'frame' },
			{ text: 'Action', value: 'action' },
			{ text: 'Contracts', value: 'contracts'},
			{ text: 'Size', value: 'size'},
			{ text: 'Date', value: 'date'},
		],

		positionHeaders: [
			{ text: '', value: 'symbol' },
			{ text: 'Position', value: 'position' },
			{ text: 'Status', value: 'order_status'},
			{ text: 'Frame', value: 'frame' },
			{ text: 'Amt', value: 'amt'},
			{ text: 'Price', value: 'price'},
			{ text: 'Date', value: 'date'},
		],

		strategies: [],
		positions: [],

	}),


	computed: {
		// formTitle () {
		//   return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
		// },
	},

	watch:{
		// editedItem: {
		//     deep: true,
		//     handler(){
		//         this.editedItem.profit = ((this.editedItem.sell_price - this.editedItem.buy_price) / this.editedItem.buy_price * 100).toFixed(2) + '%'
		//     }
		// }
	},

	created () {
		this.getPositions()
		this.getStrategies()
	},


	methods: {

		async getPositions () {
			let positions = await this.requestGet(this.$server+'/coin/bot/rangebot/positions?telegram_id=' + this.$store.state.telegramId)
			for(let item of positions){
				item.date = this.date2str(new Date(item.created_date), 'MM-dd hh:mm')
			}
			this.positions = positions
			console.log(positions)
		},


		async getStrategies () {
			let strategies = await this.requestGet(this.$server+'/coin/bot/rangebot/orders')
			for(let item of strategies){
				item.date = this.date2str(new Date(item.created_date), 'MM-dd hh:mm')
			}
			this.strategies = strategies
			// console.log(strategies)
		},


		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds()
			};

			y = y.replace(/(M+|d+|h+|m+|s+)/g, function(v) {
				return ((v.length > 1 ? "0" : "") + z[v.slice(-1)]).slice(-2)
			});

			return y.replace(/(y+)/g, function(v) {
				return x.getFullYear().toString().slice(-v.length)
			});
		},



		requestGet(uri){
			// localStorage.setItem('mexRequestModel', JSON.stringify(this.mexRequestModel));
			// let header = { headers: { sessionKey: this.mexRequestModel.key } }

			return new Promise((resolve, reject) => {
			this.$http.get(uri)
				.then((result) => {
					resolve(result.data)
				}).catch(error => {
					resolve()
				})
			})
		},


		toFixed:function(val, num){
			return Number(Number(val).toFixed(num))
		},

	},

}



</script>
